import React, { useCallback, useState } from 'react';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import update from 'immutability-helper';
import Row from './Row';
import SearchNoResults from './SearchNoResults.svg';

const Table = ({ columns, videos, setVideos, fields, loading, addNo, getVideos }: any) => {
  const [dragging, setDragging] = useState({ active: false, drag: -1, drop: -1 });

  const { data: records } = videos;

  /**
   * Trigger when user drag row into another
   * @param dragIndex
   * @param hoverIndex
   */
  const moveRow = (dragIndex: any, hoverIndex: any) => {
    const dragRecord = records[dragIndex];
    setVideos((videos: any) => ({
      ...videos,
      data: update(records, {
        $splice: [
          [dragIndex, 1],
          [hoverIndex, 0, dragRecord],
        ],
      }).map(addNo),
    }));
  };

  /**
   * Render the loading center of the table
   */
  const renderLoading = useCallback((noItems: boolean) => {
    return (
      <div className={`table-loading-overlay${noItems ? ' no-items' : ''}`}>
        <div className="loading-spinner-wrapper">
          <div
            className="spinner-grow spinner-grow-lg text-primary"
            aria-hidden="false"
            aria-label="Loading"
            role="status"
          ></div>
        </div>
      </div>
    );
  }, []);

  return (
    <DndProvider backend={HTML5Backend}>
      <div className="hb-table">
        <table className="table table-striped table-hover">
          <thead>
            <tr>
              <th style={{ width: '3%' }}></th>
              {columns.map((column: any, index1: number) => (
                <th key={index1} style={fields[column['key']]._style}>
                  {column['label']}
                </th>
              ))}
            </tr>
          </thead>
          <tbody style={{ minHeight: records.length === 0 ? (loading ? 300 : 560) : 0 }}>
            {!loading && records.length === 0 && (
              <tr className="tr-no-items">
                <td>
                  <div className="no-items-found">
                    <img src={SearchNoResults} />
                    <div>No videos were found matching your search</div>
                  </div>
                </td>
              </tr>
            )}
            {records.map((row: any, index: number) => {
              return (
                <Row
                  index={index}
                  key={row?.id}
                  fields={fields}
                  row={row}
                  moveRow={moveRow}
                  dragging={dragging}
                  setDragging={setDragging}
                  records={records}
                  setVideos={setVideos}
                  getVideos={getVideos}
                />
              );
            })}
          </tbody>
        </table>
        {loading && renderLoading(records.length === 0)}
      </div>
    </DndProvider>
  );
};

export default Table;
