import { CFormGroup, CLabel, CSpinner } from '@coreui/react';
import { KMSImg } from 'components';
import React, { useEffect, useRef, useState } from 'react';
import { SUCCESS, useApi } from 'store/callApi';
import { encodeFilename } from 'utils';
import upload from './upload.png';

interface Props {
  videoId: string;
  thumbnail: any;
  setThumbnail: (value: any) => void;
  loading: boolean;
  setLoading: (param: any) => void;
}

export const MAX_THUMBNAIL_SIZE = 52428800;

const UploadThumbnail: React.FC<Props> = (props) => {
  const { videoId, thumbnail, setThumbnail, loading, setLoading } = props;
  const { callApi } = useApi();

  const [uploadThumbnailError, setUploadThumbnailError] = useState(false);

  /**
   * Reset error when change modal
   */
  useEffect(() => {
    if (!videoId) return;

    setUploadThumbnailError(false);
  }, [videoId]);

  const uploadThumbnailRef = useRef<any>(null);

  /**
   * After select image, upload image to server and save info to form
   * @param e input event
   */
  const uploadThumbnail = (e: any) => {
    let reader = new FileReader();
    let file = e.target.files[0];
    if (!file) return;

    if (file.size > MAX_THUMBNAIL_SIZE) {
      setUploadThumbnailError(true);
      return;
    }

    let link_api = '';
    let link_thumbnail = '';

    reader.onload = () => {
      setThumbnail(reader.result);
      setLoading(true);
      callApi(
        {
          method: 'get',
          api: `/api/v1/upload/image-video-thumbnail?file_name=${encodeFilename(file.name)}&video_id=${videoId}`,
        },
        (response: any) => {
          if (response.status === SUCCESS) {
            link_api = response?.data?.link;
            link_thumbnail = response?.data?.file_path?.link;
            let myHeaders = new Headers();
            myHeaders.append('x-amz-acl', 'public-read');
            const requestOptions: any = {
              method: 'PUT',
              headers: myHeaders,
              body: file,
              redirect: 'follow',
            };
            fetch(link_api, requestOptions)
              .then((response) => response.text())
              .then(() => {
                setLoading(false);
                setThumbnail(link_thumbnail);
              })
              .catch((error) => console.log('error', error));
          } else {
            setLoading(false);
          }
        },
      );
    };
    reader.readAsDataURL(file);

    // Reset input
    e.target.value = null;
  };

  /**
   * Render the remove button at corner of thumbnail
   */
  const renderBtnRemoveThumbnail = () => {
    // When user click this button, remove thumbnail of video
    const handleClick = () => {
      setThumbnail('');
    };

    return <i onClick={handleClick} className="fas fa-times-circle btn-remove" />;
  };

  return (
    <CFormGroup>
      <CLabel>Thumbnail</CLabel>
      <div className="modal-thumbnail">
        {thumbnail ? (
          <div className="thumbnail-wrapper">
            <KMSImg src={thumbnail} />
            <div className="thumbnail-overlay" />
            {renderBtnRemoveThumbnail()}
            {loading && <CSpinner className="thumbnail-spinner" />}
          </div>
        ) : (
          <div className="upload-wrapper" onClick={() => uploadThumbnailRef.current?.click?.()}>
            <div className="upload-icon-wrapper">
              <img className="icon-upload" src={upload} />
              <div className="text-center">Upload Thumbnail</div>
              <input
                type="file"
                id="custom_thumbnail"
                name="custom_thumbnail"
                ref={uploadThumbnailRef}
                style={{ display: 'none' }}
                accept="image/*"
                onChange={uploadThumbnail}
              />
            </div>
            {uploadThumbnailError && (
              <div className="upload-file-error">
                <i className="fas fa-exclamation-triangle mr-2" />
                <div style={{ width: 94 }}>Cannot upload file larger than 50MB</div>
              </div>
            )}
          </div>
        )}
      </div>
    </CFormGroup>
  );
};

export default UploadThumbnail;
