import { MaxLine } from 'components';
import moment from 'moment';
import React, { useRef } from 'react';
import { useDrag, useDrop } from 'react-dnd';
import { useApi } from 'store/callApi';

const DND_ITEM_TYPE = 'row';

const Row = ({ row, index, moveRow, fields, dragging, setDragging, records, setVideos, getVideos }: any) => {
  const dropRef = useRef<any>(null);
  const dragRef = useRef<any>(null);

  const { callApi } = useApi();

  const [, drop] = useDrop({
    accept: DND_ITEM_TYPE,
    hover(item: any, monitor: any) {
      if (!dropRef.current) {
        return;
      }

      const dragIndex = item.index;
      const hoverIndex = index;

      if (!dragging.active) setDragging({ active: true, drag: records[dragIndex].priority, drop: -1 });

      // Don't replace items with themselves

      if (dragIndex === hoverIndex) {
        return;
      }
      // Determine rectangle on screen
      const hoverBoundingRect = dropRef.current?.getBoundingClientRect();
      // Get vertical middle
      const hoverMiddleY = (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
      // Determine mouse position
      const clientOffset = monitor.getClientOffset();
      // Get pixels to the top
      const hoverClientY = clientOffset.y - hoverBoundingRect.top;
      // Only perform the move when the mouse has crossed half of the items height
      // When dragging downwards, only move when the cursor is below 50%
      // When dragging upwards, only move when the cursor is above 50%
      // Dragging downwards
      if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
        return;
      }
      // Dragging upwards
      if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
        return;
      }

      if (dragging.drag !== records[hoverIndex].priority)
        setDragging((dragging: any) => ({ ...dragging, drop: records[hoverIndex].priority }));

      // Time to actually perform the action
      moveRow(dragIndex, hoverIndex);
      // Note: we're mutating the monitor item here!
      // Generally it's better to avoid mutations,
      // but it's good here for the sake of performance
      // to avoid expensive index searches.
      item.index = hoverIndex;
    },
    drop() {
      if (dragging.drag === dragging.drop) return;

      setVideos((videos: any) => ({ ...videos, loading: true }));
      callApi(
        {
          method: 'put',
          api: '/api/v1/admin/video/priorities',
          body: { position_drag: dragging.drag, position_drop: dragging.drop },
        },
        () => getVideos(),
      );
      setDragging((dragging: any) => ({ ...dragging, active: false }));
    },
  });

  const [{ isDragging }, drag, preview] = useDrag({
    type: DND_ITEM_TYPE,
    item: { type: DND_ITEM_TYPE, index },
    collect: (monitor) => ({
      isDragging: !!monitor.isDragging(),
    }),
  });

  const opacity = isDragging ? 0 : 1;

  preview(drop(dropRef));
  drag(dragRef);

  return (
    <tr ref={dropRef} style={{ opacity }}>
      <td style={{ width: '3%' }} ref={dragRef}>
        <i className="fas fa-arrows-alt-v" style={{ color: '#777', fontSize: 18 }} />
      </td>
      {Object.keys(fields).map((cell: string, index: number) => {
        const columnName = cell;
        const field = fields[columnName];
        let newContent = row[columnName];
        let newRenderContent;
        if (field.isDate) newRenderContent = !newContent ? '-' : moment(newContent * 1000).format('DD/MM/YYYY');
        else {
          if (field.renderContent) newRenderContent = field.renderContent(row);
          else newRenderContent = newContent;
        }
        return (
          <td key={index} style={field._style}>
            {field.disableMaxLine ? newRenderContent : <MaxLine text={newRenderContent} />}
          </td>
        );
      })}
    </tr>
  );
};

export default Row;
